.AuthRegister{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow: auto;
    line-height: 1rem !important;
}

.AuthRegister::-webkit-scrollbar{
    display: none;
}
.Register{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    width: 90%;
    align-items: center;
    margin: 0 auto;
    overflow: auto;
}


.register__card{
    display: flex;
    flex-direction: column;
    box-shadow: 5px 1px 22px 5px rgba(99, 4, 121, 0.2);
    width: 50%;
    border-radius: 8px;
    padding-bottom: 1rem;
    margin-bottom: 2.2rem;
    overflow: auto;
    
}

.register__card::-webkit-scrollbar{
    display: none;
}
.register__card h3{
    font-size: .8rem;
    text-align: center;
    text-transform: uppercase;
    color: rgb(108, 98, 110);
    padding: 1rem 0 .2rem 0;
}
.register__card form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: .6rem;
    width: 100%;
    margin: 0 auto;
    
}

.register__card form label{
    color: purple;
    font-size: 1rem;
    font-weight: 600;
    margin-left: 1.5rem;
}

.register__card input{
    border: 1px solid var(--nfcs-bg);
    outline: none;
    padding: 5px;
    font-size: .9rem;
    font-weight: 500;
    color: grey;
    background: rgb(251, 230, 253) ;
    width: 85%;
    margin: 0 auto;
}

.register__card input{

}

.register__card input:hover{
    border: 4px solid var(--nfcs-bg);
    transition: .2s ease-in-out;
    border-radius: 3px;
}

.checkbox{
    display: flex;
    justify-content: center;
    width: 95%;
    gap: .5rem;
    color: red !important;
}

.row_input{
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
}

.checkbox input{
    background-color: purple !important;
    width: 40px;
    padding: 0;
    margin: 0;
    margin-right: -1.7rem;;
}

.checkbox label{
    font-size: .7rem !important;
    line-height: .8rem;
}
.register__card form .MuiButtonBase-root{
    font-weight: 600;
    font-size: 1rem;
    width: 80%;
    margin: 0 auto;
    color: purple;
    text-transform:capitalize;
}
.register__card form .MuiButtonBase-root:hover{
    background-color: var(--btn-bg-hover);
    color: purple;
    text-transform:capitalize;
}
.register__card form p {
    font-size: .8rem;
    font-weight: 500;
    color: gray;
    padding:0 ;
    margin-left: 8px;
}

.link_login{
    padding: 10px;
    margin-left: 1.5rem;
    margin: 0;
}

.register__card form p a{
    color: rgb(112, 17, 150);
    text-decoration: none;
}

.register__card form .errorMsg{
    color: red;
    padding: 0;
    margin: 0;
    font-weight: 500;
    font-size: .7rem;
    margin-left: 2rem;
    width: 85%;
    line-height: 1rem;
    text-align: justify;
}

.visitor{
    margin: 0;
    padding: 0;
    text-align: center;
}
@media screen and (max-width: 800px){
    .register__card{
        transition: .5s all ease-in-out;
        width: 75%;
    }
}

@media screen and (max-width: 500px){
    .register__card input{
        padding: 7px;
        transition: .3s all;
    }
    .Register{
        height: 80vh;
        transition: .5s all ease-in-out;
    }
    .register__card{
        width: 97%;
    }
    .register__card form .errorMsg{
        margin-left: 1rem;
        text-align: justify;
        width: 80%;
        line-height: 1rem;
    }
}
@media screen and (max-width: 450px){
    .register__card{
        width: 98%;
        transition: .5s all ease-in-out;
    }
}
@media screen and (max-width: 400px){
    .register__card{
        overflow: auto;
        min-width: 200px;
    }
    .checkbox{
        justify-content: center;
        margin: 0 auto;
        margin-left: .2rem;
    }

    .errorMsg{
        margin-left: 1rem;;
    }
}