.Requests{
    display: flex;
    width: 100%;
    height: 100vh;
    overflow:auto;
}
.Requests::-webkit--scrollbar{
    display:none;
}

.cont{
    width: 100%;
    display: flex;
    flex-direction: column;
    display: flex;
    align-items: center;
    padding: 5rem 0rem 0rem 0rem;
}

.cont h3{
    color: grey;
    letter-spacing: 2px;
    padding: .5rem;
}

.cont_card{
    display: flex;
    width: 90%;
    flex-direction: column;
    margin: 0 auto;
    }

.cont_card form p{
    color: rgb(255, 78, 166);
    font-size: 1rem;
    font-weight: 400;
    text-align: center;
}

.input_field{
    display: flex;
    flex-direction: row;
    width: 70%;
    background: var(--nfcs-bg);
    overflow: hidden;
    margin: 2rem auto;
    border-radius: 20px;
    padding: 4px;
}

.input_field input{
    background-color: var(--nfcs-bg);
    outline: none;
    width: 80%;
    border-right:  none;
    border-left: none;
    border-top: none;
    border-bottom: none;
    border-radius: 0 20px 20px 0;
    padding: 4px 4px 4px 7px;
    font-size: 1rem;
    font-weight: 400;
    color: grey;
    outline: none;
}
.input_field .MuiButtonBase-root{
    width: 30%;
}
.login__button .MuiSvgIcon-root {
    color: rgb(71, 70, 71);
}



/* For password reset component */
.reset_card{
    width: 90%;
}
.reset_card form{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 70%;
    margin: 0 auto;
}


.reset_card form p{
    color: rgb(223, 141, 224);
    font-size: 1rem;
    font-weight: 500;
}

.reset_card form code{
    color: red;
    font-size: 1rem;
    font-weight: 600;
}
.reset_card form input{
    border: none;
    outline: none;
    background: var(--nfcs-bg);
    padding: 5px;
    font-size: 1rem;
    color: grey;
    font-weight: 400;
    border-radius: 15px;

}





@media screen and (max-width: 500px) {
    .input_field{
        width: 95%;
    }
    .input_field input{
        width: 80%;
        padding: 6px;
    }
    .cont_card{
        width: 100%;
    }
    .cont{
        padding: 4rem 1rem 1rem 1rem;
    }
    .input_field .MuiButtonBase-root{
        width: 20%;
        min-width: 75px;
        font-size: .7rem;
        padding-left: 3px;
    }
    
.cont_card form p{
    font-size: .8rem;
    font-weight: 600;
}

.reset_card{
    width: 100%;
}
.reset_card form{
    width: 90%;
}
.reset_card{
    width: 100%;
}
.reset_card form{
    width: 90%;
}
}