nav{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.nav{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 1s all;
    z-index: 99999;
    background-color: rgb(0, 0, 0);
    padding: 1rem .3rem 0rem .2rem;
}

.nav ul{
    display: flex;
    flex-direction: row;
    gap: .3rem;
}

.nav ul li{
    font-size: .1rem;
    font-weight: 500;
    list-style: none;
    padding: .6rem;
    color: white;
}
.nav ul li a{
    text-decoration: none;
    color: rgb(247, 216, 247);
    margin: 1rem;
}

.nav .MuiButtonBase-root{
    padding: 3px;
    padding-left: 0;
}
.nav .MuiButtonBase-root img{
    width: 30px;
    padding: 0px !important;
    margin: 0;
}
.navigation span{
    text-transform: uppercase;
    font-size: 600;
}

.navLinks ul li a{
    text-decoration: none;
    color: var(--nfcs-txt);
}

.navLinks ul li .MuiSvgIcon-root{
    color: red;
    width: 30px;
    
}
.nav a:hover{
    color: rgb(215, 7, 223);
    transition: .3s ease-in-out;
    cursor: pointer;
}

.nav a{
    color: rgb(247, 216, 247);
    padding: 0 0 0 .3rem;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 500;
}
.barIcon{
    display: none;
}
.dropdown{
    display: none;
}


@media screen and (max-width: 700px){
    .nav{
        background-color: rgb(8, 0, 10);
        padding: .2rem 0 0 0;
        color: white;
    }
    .nav ul{
        display: none;
    }

    .nav a{
        color: rgb(255, 190, 233);
    }
    .barIcon{
        display: flex;
        margin-right: 1.5rem;
        transition: all 0.3s;
        padding-right: .5rem;
    }
    .login{
        font-style: capitalize !important;
    }
    .barIcon .MuiSvgIcon-root{
        color: rgb(255, 190, 233);
        font-size: 2.5rem;
        margin-right: .5rem;
    }

    .barIcon img{
        width: 45px !important;
    }
    .rotate{
        transform: rotate(-90deg);
        background: yellow;
        transition: all 0.2s ease-in-out;
    }
    .dropdown{
        display: flex;
        flex-direction: column;
        align-items: center; transition: 1s all;
        border-bottom: .5px solid rgba(98, 0, 0, 0.213);
    } 

    .dropdown span{
        display: flex;
        flex-direction: column;
        padding: .5rem 0 .5rem 0;
        align-items: center;
        color: purple;
        width: 100% !important;
        font-size: 1.1rem;
        font-weight: 600;
        cursor: pointer;
       
    }
    .dropdown span:hover{
        background: rgba(8, 0, 10, 0.8);
        color: white;
        transition: .4s all;
        font-size: 1.1rem;
    }
    .dropdown span a{
        text-decoration: overline;
        font-size: 1rem;
        font-weight: 600;
        cursor: pointer;
        transition: .4s all;
        width: 100%;
        text-align: center;
    }
    .dropdown span a:hover{
        color: var(--nfcs-bg);
        font-size: 1.3rem;
    }
    .dropdown ul li:hover{
        transform: scale(1.2);
        transition: all 0.3s;
    }
    .barIcon .MuiSvgIcon-root:hover{
        color: var(--nfcs-link);
        transition: .4s ease-in-out;
        font-size: 2.4rem;
    }
    .off{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 15rem;
        height: 100vh;
        background: rgba(255, 255, 255, 0.098);
        z-index: 999999;
        position: absolute;
    
    }
}

.logout_btn{
    z-index: 9999;
    color: white;

}

@media screen and (max-width: 900px){
    .navLinks ul{
        gap: 1.5rem;
        font-size: 1.2rem;
    }
    
}
@media screen and (max-width: 500px){
    .nav .MuiButtonBase-root{
        padding: 4px;
        padding-left: 0;
    }
    .nav .MuiButtonBase-root img{
        width: 35px;
        padding: 0px !important;
        margin: 0;
    }
 
}
@media screen and (max-width: 393px){
}
@media screen and (max-width: 100px){
    .barIcon{
        display: none;
    }
    .dropdown{
       display: none;
    }
}