.detail{
    background-color: rgb(253, 246, 255);
    width: 100%;
}
.detail::-webkit-scrollbar{
    display: none;
}

.detail h3{
    font-size: 2rem !important;
    text-align: left;
    padding: 1rem 0 0 1rem;
    font-size: 1.5rem;
    color: rgb(37, 11, 37);
    text-transform: capitalize;
    letter-spacing: .1rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.headerImage{
    position: relative;
    background-size: cover no-repeat;
    background-position: center center;
    width: 100%;
    height: 300px;
    z-index: 0;
}

.headerImage h1{
    width: 38%;
    text-align: center;
    margin-left: -3px;
    font-size: 1rem !important;
    font-weight: 500;
    padding: .5rem;
    border-radius: 0 2rem 2rem 0;
}

.headerImage h2{
    color: rgb(255, 17, 227);
    padding: 1.5rem 0 0 2rem;
    z-index: 1;
}
.headerImage span{
    color: pink;
    font-size: 1.1rem;
    padding: 2rem 0 0 3rem;
    z-index: 1;
}

.fade_image{
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    margin-bottom: 0.05;
    display: flex;
    height: 1.7rem;
    background-image: linear-gradient(180deg, transparent, rgb(255, 245, 252));
    z-index: 999;
}
.detail_body{
    display: flex;
    flex-direction: column;
    width: 95%;
    margin-top: 1rem;

}

.article{
    font-size: 1rem;
    color: rgb(6, 4, 0);
    font-weight: 400;
    line-height: 2rem;
    text-align: justify;
    font-family:Georgia, 'Times New Roman', Times, serif;
}

.nav_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.nav_btn a{
    text-decoration: none;
}
.nav_btn .MuiButtonBase-root{
    background: rgb(255, 250, 250);
    font-weight: 400 !important;
    color: grey !important;
}
@media screen and (max-width: 500px){
    .detail h3{
        text-align: center;
    }
    .detail_body{
        width: 90%;
    }
    .article{
        font-size: 1.3rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: -2.5rem;
        z-index: 1;
    }
    .fade_image{
        position: sticky bottom;
        margin-bottom: 0.005rem;
    
    }
    
.headerImage h1{
    font-size: 100%;
    min-width: 105px;
    overflow: hidden;
}
}
