.activate{
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #ffdffa;
    background-image: url('../Images/v.jpg');
    background-size: cover;
    padding-top: 6rem;
    height: 100vh;
}

.activate h3{
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 1rem;
    text-transform: uppercase;
    letter-spacing: .2rem;
    border: .6px solid white;
    padding: 8px;
    background: rgba(255, 253, 253, 0.654);
    width: 50%;
    margin: 0 auto;
    color: rgb(63, 0, 63);
}

.activate .MuiSvgIcon-root{
    color: rgb(81, 108, 81);
    font-size: 5rem;
}


.contain{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1.2rem;
    padding-top: 5rem;

}

.contain h4{
    text-align: center;
    font-size: 1.3rem;
    color: #0d3e38;
}

@media screen and (max-width: 800px){
    .activate h3{
        width: 70%;
        font-size: 1rem;
        font-weight: 500;
    }
}