.Category{
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-flow: row wrap;
    gap: .1rem;
    line-height: .5rem;
    justify-content: space-around;
    padding-top: .2rem;
}

.Category span{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border: 1px solid var(--nfcs-bg);
    cursor: pointer;
    padding-bottom: 0px;
}
.Category span .MuiButtonBase-root{
    color: grey;
    padding: 1px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-size: .7rem;
}   

.Category .cat_span .MuiButtonBase-root a {
    text-decoration: none;
    color: grey;
}

.line{
    display: flex;
    width: 95%;
    margin-top: .5rem;
    height: 2px;
    background-color: rgba(170, 123, 187, 0.242);
    border-radius: 5rem;
}

.flex_categ{
    display: flex;
    flex-flow: wrap row;
    background-color: rgb(255, 255, 255);
}

.categ{
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
}

.catory_card{
    display: flex;
    width: 30%;
    height: 150px;
    overflow: hidden;
    margin: .5rem auto;
    background: rgba(0, 0, 0, 0.155);
    
}

.card_left{
    width: 95%;
    padding: 5px;
}
.card_left h2{
    font-size: .8rem;
    color: grey;
    font-weight: 400;
}

.card_left h3{
    font-size: 1rem;
    text-align: left;
    color: rgb(255, 77, 0);
}

.card_left p{
    font-size: .8rem;
    padding: 0;
    margin: 0;
    line-height: 1.2rem;
    width: 100%;
    text-align: left;
}
.card_right{
height: 250px;
}

.card_right img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 3rem 0 0 25rem;
}


@media screen and (max-width: 900px){
    .catory_card{
        width: 28%;
    }
}

@media screen and (max-width: 630px){
    .catory_card{
        width: 43%;
        margin: .8rem;
    }
}

@media screen and (max-width: 450px){
    .catory_card{
        width: 90%;
        margin: .3rem auto;
    }
}

.error_width{
    width: 100%;
    display: flex;
}

.error_card{
    color: red;
    font-weight: 600;
    display: flex;
    justify-content: center;
    margin: 4rem auto;
}

.loading{
    width: 100%;
    margin: 4.5rem auto;
}

.loading_spin{
    font-weight: 600;
    display: flex;
    justify-content: center;
    margin: 4rem auto;   
}

.categ{
    width: 100%;
    display:flex;
    max-width: 1200px;
}

.categ_name{
    width: 50%;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-top: 2rem;
    margin-left: 1rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 1.2rem;
    color: rgb(70, 1, 70);
}