.transfar_container{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1rem;
}


.transfar_container input{
    display: flex;;
    width: 80%;
    margin: 0 auto;
}
.assureIcon{
    display: flex;
    flex-direction: row;
    gap: .5rem;
    align-items: center;
}

.assureIcon .MuiSvgIcon-root{
    color: rgb(17, 125, 17);
    font-size: 2.5rem;
}

.assureIcon h4{
    font-size: 1rem;
    color: grey;
    padding: 0;
    margin: 0;
}

.bank_details{
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 7px;
}

.bank_details span{
    font-size: .8rem;
    font-weight: 600;
    color: grey;
}

.bank_details span strong{
    text-transform: uppercase;
    font-size: .750rem;
    padding-left: 5px;
    color: rgb(82, 80, 80);
}

.bank_details span b{
    background: #f2dceb;
    color: rgb(59, 59, 59);
    padding: 3px;
    margin-left: 3px;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
}

.copy{
    position: absolute;
    z-index: 100;
    color: white !important;
    background: black;
    border-radius: 5px;
    padding: 3px;
    top: 0;
    right: 5;
    
}