.donate_container{
    background: linear-gradient(to bottom, rgb(37, 0, 0), rgba(25, 0, 0, 0.666) 100%);
    color: white;
    width: 100%;
    height: 100vh;
    max-width: 1200px;
    margin: 0 auto;
}

.upper_header{
    position: relative;
    background-image: url('../Images/church.webp') !important;
    background-size: cover;
    background-position: left;
    background-blend-mode:multiply;
    background-repeat: no-repeat;
    width: 100%;
    z-index: 0;
    height: 80%;
    overflow: auto;
}

.upper_header::-webkit-scrollbar{
    display: none;
}
.fade_images{
    background-image: linear-gradient(180deg, transparent, rgb(189, 176, 176)); 
    width: 100%;
    margin-top: -1rem;
    z-index: 200;
    height: 50px;
    content: 'Hi there';
}

.artiq{
    padding: 1rem;
    text-align: left;
    width: 90%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.artiq h2{
    width: 80%;
    margin-top: .5rem;
    margin-bottom: .7rem;
    line-height: 28px;
    text-shadow: -4px 0px 0.5px rgb(191 97 11 / 87%);
    padding: 2px;
    color: #ffffff;
}

.artiq p{
    font-size: .8rem;
    width: 90%;
}

.artiq h3{
    color: rgb(210, 85, 255);
    text-shadow: -4px 0px 0.5px rgb(0 0 0 / 87%);
}

.sub_header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* margin: .3rem .5rem 0rem .5rem; */
    overflow: hidden;

}

.icon_circle{
    width: 70px;
    height: 70px;
    border-radius: 50px;
    background: white;
}
/* background-image: linear-gradient(180deg, transparent, rgb(255, 245, 252)); */

.lower{
    width: 100%;
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: right;
}

.lower .MuiButtonBase-root
{
    color: white;
    background: purple;
    margin: 1rem;
}


.MuiBox-root{
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    margin: 4rem auto;
    margin-top: 4rem auto;
    background: #f9efff !important;
    width: 80% !important;
    height: 80%;
    overflow: auto;
}

.cancel{
    position: absolute;
    right: 2%;
    top: 1%;
    z-index: 100;
}

.cancel .MuiSvgIcon-root{
    color: red;
    font-size: 2rem;
    cursor: pointer;
}

.cancel .MuiSvgIcon-root:hover{
    font-size: 2.5rem;
    color: rgb(255, 194, 194);
    transition: all .4s ease-in;
}
.pay_body{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    height: 100%;
    margin-top: 0;
    overflow: auto;
}

.pay_body h2{
    color: grey;
    font-size: 1rem;
    margin-top: 0;
    text-align: left;
}

.pay_body::-webkit-scrollbar{
    display: none;
}

.MuiBox-root form{
    display: flex;
    flex-direction: column;
    margin: .5rem auto;
    width: 80%;
}

.MuiBox-root form input{
    margin: .3rem;
    padding: 5px;
    border: none;
    outline: none;
    background: #faeaed;
    color: grey;
}

.MuiBox-root form textarea{
    border: none;
    outline: .2px solid rgb(232, 232, 232);
    padding: 7px;
    font-size: 1.08rem;
    color: grey;
    font-family:'Times New Roman', Times, serif;
    height: 50px;
}

.MuiBox-root form label{
    color: grey;
    font-weight: 500;
}

.select{
    display: flex;
    border: 1px solid rgb(255, 236, 236);
    justify-content: space-between;
    padding: 5px;
}

.select div{
    font-size: 1rem;
    font-weight: 650;
    color: grey;
}

.drop_down{
    display:flex;
    flex-direction: column;
    text-align: center;
    text-transform: uppercase;
    background-color: black;
    font-weight: 600;
    color: white;
}

.drop_down:hover{
    background: white;
    color: green;
    transition: all .3s ease-in-out;
    cursor: pointer;
}
@media screen and (max-width: 500px){
    .upper_header{
        overflow: auto;
    }

    .upper_header::-webkit-scrollbar{
        display: none;
    }

    .artiq h2{
        font-size: 1.2rem;
    }

    .MuiBox-root{
        height: 100%;
    }
    .pay_body{
        height: 100%;
    }
}

.submit_btn{
    display: flex;
    justify-content: right;
}

.submit_btn .MuiButtonBase-root{
    background: purple;
    color: white;
}



@media screen and (max-width: 900px){
    .MuiBox-root{
        margin: 2rem auto;
        margin-top: 4.3rem;
        height: 70% !important;
    }
    .MuiBox-root form{
        width: 100%;
    }
    .submit_btn{
        margin-bottom: 1.5rem;
    }
}